import { ChangeDetectionStrategy } from '@angular/compiler';
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { ApiService } from 'src/app/services/api.service';
import { DataService } from 'src/app/services/data.service';
import { FingerprintAIO } from '@ionic-native/fingerprint-aio/ngx';
import { isPlatform, Platform, ToastController } from '@ionic/angular';
import { FormGroup, FormBuilder } from '@angular/forms';
import { Router } from '@angular/router';


@Component({
  selector: 'app-securitycenter',
  templateUrl: './securitycenter.page.html',
  styleUrls: ['./securitycenter.page.scss'],
})
export class SecuritycenterPage implements OnInit {
  title: string = 'Security Center';
  mpin: string;
  setmpinFlag: boolean = true;
  currentUser: any;
  isFaceId: boolean = false;
  isFingerPrint: boolean = false;
  isDevice: boolean;
  bioForm: FormGroup;
  isBioFeature: boolean = false;
  isAndroid: boolean = false;

  constructor(private router: Router,
    private cdr: ChangeDetectorRef,
    private dataService: DataService,
    private toastController: ToastController,
    private faio: FingerprintAIO,
    public platform: Platform,
    private fb: FormBuilder,
    private apiProvider: ApiService) {

  }

  ngOnInit() {
    this.buildBioAuthStateForm();
    this.currentUser = this.dataService.getCurrentUser();
    this.isDevice = this.platform.is('cordova');
    this.platform.ready().then(() => {
      if (this.platform.is("android")) {
        this.isAndroid = true;
      }
      if (this.platform.is('ios') || this.platform.is("iphone") || this.platform.is("ipad")) {
        this.isAndroid = false;
      }
    });

    this.cdr.markForCheck();
  }

  buildBioAuthStateForm() {
    this.bioForm = this.fb.group({
      faceId: [null],
      fingerPrint: [null]
    });
  }

  ionViewWillEnter() {
    this.currentUser = this.dataService.getCurrentUser();
    this.isFaceId = this.currentUser?.faceLoginEnable ? true : false;
    this.isFingerPrint = this.currentUser?.fingerprintLoginEnable ? true : false;
    this.setmpinFlag = this.currentUser?.mpinActive ? true : false;
  }


  goToHome() {
    this.router.navigate(['/profile']);
  }

  setmpin() {
    this.router.navigate(['/setmpin']);
  }

  changempin() {
    this.router.navigate(['/changempin']);
  }

  changePassword() {
    this.router.navigate(['/changepassword']);
  }
  goToLoginActivity() {
    this.router.navigate(['/loginactivity']);
  }

  updateFinger(e: any) {
    console.log(e);
    let title = 'fingerprint';
    if (this.bioForm.get('fingerPrint')?.value) {
      this.checkHardwareAvailability(title, true);
    } else {
      this.setBioAuth(title, false);
    }
  }

  updateFaceId(e: any) {
    let title = 'faceId';
    if (this.bioForm.get('faceId')?.value) {
      this.checkHardwareAvailability(title, true);
    } else {
      this.setBioAuth(title, false);
    }
  }

  checkHardwareAvailability(title: string, status: boolean) {
    this.faio.isAvailable().then((result: any) => {
      console.log("result of bio", result);
      if (result === "finger" || result === "face" || result === "biometric") {
        this.isBioFeature = true;
        this.setBioAuth(title, status);
      } else {
        this.isBioFeature = false;
      }
    }).catch((error: any) => {
      console.log(error);
      this.presentToast("BIOMETRIC UNAVAILABLE!");
    });
  }

  setBioAuth(title: string, status: boolean) {
    let payload = {
      customerId: this.currentUser?.customerId,
      faceLoginEnable: this.bioForm.get('faceId')?.value,
      fingerprintLoginEnable: this.bioForm.get('fingerPrint')?.value
    };
    this.apiProvider.registerFaceIdORFingerPrint(payload).subscribe((res: any) => {
      localStorage.setItem(
        'customer_details',
        JSON.stringify(res.data)
      );
      this.dataService.isLoggedIn.next(true);
      if (status) {
        if (this.isFaceId || this.isFingerPrint) {
          return;
        } else {
          this.presentToast(`${title} Enabled Successfully!`);
        }
      } else {
        this.presentToast(`${title} Disabled Successfully!`);
      }
      this.cdr.markForCheck();
    }, (err: any) => {
      console.log(err);
    });
  }


  async presentToast(msg: any, position?: any) {
    const toast = await this.toastController.create({
      message: msg,
      duration: 800,
      position: position || 'bottom'
    });
    toast.present();
  }

}
